import { Component, OnInit } from '@angular/core';
import {MYBOOKS} from '../../assets/data/myBooks';
import { bookInfo } from '../bookInfo';

@Component({
  selector: 'app-lekoluka',
  templateUrl: './lekoluka.component.html',
  styleUrls: ['./lekoluka.component.css']
})
export class LekolukaComponent implements OnInit {

  mybooks = MYBOOKS;
  pickedBook : bookInfo;

  constructor() { }

  ngOnInit() {
    this.onButtonClick(0);
  }

  onButtonClick(index){
    this.pickedBook = {
      bookName: this.mybooks[index].bookName,
      author: this.mybooks[index].author,
      genre: this.mybooks[index].genre,
      yearPublished: this.mybooks[index].yearPublished,
      pictureUrl: this.mybooks[index].pictureUrl,
    }
    return this.pickedBook;
  }

  

}
